body {
  color: white;
  background: black;
 }
 div {
  display: flex;
  flex-direction: column;
  text-align: center;
  align-items: center;
  padding: 30px;
 }
 input {
  width: 0.1px;
  height: 0.1px;
  opacity: 0;
  overflow: hidden;
  position: absolute;
  z-index: -1;
 }

 select {
  width: 200px;
  box-sizing: border-box;
  border: solid 1px white;
  background: black;
  color: white;
  font-size: 24px;
  margin: auto;
  padding: 10px;
  margin-right: 50px;
 }


 button,
 input,
 label,
 ul {
  width: 300px;
  box-sizing: border-box;
  border: solid 1px white;
  background: black;
  color: white;
  font-size: 24px;
  margin: auto;
  padding: 10px;
 }
 img {
  width: 300px;
  margin-bottom: 25px;
 }
 ul {
  list-style: none;
  padding: 10px;
  margin-bottom: 25px;
 }